import {Component} from '@angular/core';
import {IonicPage, LoadingController, NavController} from 'ionic-angular';
import 'firebase/firestore';
import {NoticeModel} from "../../models/notice-model";

import {NoticeProvider} from "../../providers/notice/notice-provider";
import {FunctionsProvider} from "../../providers/functions/functions";
import {AngularFireAuth} from 'angularfire2/auth';


@IonicPage()
@Component({
  selector: 'notice-page',
  templateUrl: 'notice.html'
})
export class NoticePage {
  messages: any;
  private db: any;
  model: any = {};
  private notices : NoticeModel;
  private bid: any = "1";


  constructor(public navCtrl: NavController,
              private  loading:LoadingController,
              private notice: NoticeProvider,
              private  func: FunctionsProvider,
              private  afs: AngularFireAuth,


  ){

      this.afs.authState.subscribe(res => {
          if (res && res.uid) {

              this.func.sync(res.uid,this.bid)

          } else {

              this.navCtrl.setRoot('login',{
                  refresh:"true",
              });
          }
      });

             this.loadData()
  }


    async loadData(){

        this.notice.query().then((res: any) => {

            this.notices= JSON.parse(res.data)




        }).catch(er=> console.log(er))
        ;
    }





  openAnnounce(s: any) {
    this.navCtrl.push('NoticeDetailPage', {

      data: s
    });
  }


}



